import { __decorate } from "tslib";
import { Component, Vue } from 'vue-property-decorator';
import page from '@/components/common-components/page.vue';
import imageUpload from '@/components/common-components/image-upload.vue';
import { EventBus } from '@/until/event-bus';
let AppList = class AppList extends Vue {
    constructor() {
        super(...arguments);
        this.title = '新增商品';
        this.model = {
            name: '',
            price: 0,
            unit: '',
            disabled: false,
            number: 0,
            imgUrlList: []
        };
        this.fields = [
            {
                type: 'input',
                modelKey: 'name',
                label: '商品名称',
                props: {
                    placeholder: '请输入'
                },
                rules: {
                    required: true
                },
                // validating when blur
                trigger: 'blur'
            },
            {
                type: 'input',
                modelKey: 'price',
                label: '价格',
                props: {
                    placeholder: '请输入价格'
                },
                rules: {
                    required: true,
                    type: 'number'
                },
                // validating when blur
                trigger: 'blur'
            },
            {
                type: 'select',
                modelKey: 'unit',
                label: '单位',
                props: {
                    options: ['个', '斤', '件', '箱', '平米', '条']
                },
                rules: {
                    required: true
                }
            },
            {
                type: 'switch',
                modelKey: 'disabled',
                label: '是否上架'
            },
            {
                type: 'input',
                modelKey: 'number',
                label: '数量',
                props: {
                    placeholder: '请输入数量'
                },
                rules: {
                    type: 'number',
                    required: true
                },
                // validating when blur
                trigger: 'blur'
            }
        ];
        this.options = {
            scrollToInvalidField: true,
            layout: 'standard' // classic fresh
        };
    }
    created() {
        let params = this.$route.params;
        this.classId = params.classId;
        this.shopId = params.shopId;
        if (params.data.id) {
            this.title = '修改商品';
            this.model = params.data;
        }
    }
    validateHandler() {
        console.log('ddd');
    }
    submitHandler(e) {
        e.preventDefault();
        let businessId = this.$store.state.userInfo.business;
        let queryObj;
        queryObj = { ...this.model, businessId: businessId, class: this.classId, shopId: this.shopId };
        this.$store.dispatch('addOrUpdateCommodity', queryObj).then((response) => {
            if (response.success) {
                if (response.data) {
                    EventBus.$emit("commodityManage", { type: 'add', data: response.data });
                }
                else {
                    EventBus.$emit("commodityManage", { type: 'update', data: queryObj });
                }
                this.$router.back();
                const toast = this.$createToast({
                    txt: '保存成功',
                    time: 3000,
                    type: 'txt'
                });
                toast.show();
            }
        });
    }
    submitClick() {
        this.$refs.cubeForm.submit();
    }
    resetHandler() {
        this.$emit('cancel');
    }
};
AppList = __decorate([
    Component({
        components: {
            page,
            imageUpload
        }
    })
], AppList);
export default AppList;
